.radio {
  width: 46px;
  height: 46px;
  border-radius: 50%;
  background-color: #f2f2f2;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

input[type="radio"]:checked + .radio::before {

  width: 16px;
  height: 16px;
  border-radius: 50%;
  background-color: #333333;
  content: "";
  display: block;
}
